// Ici, on retrouvera tout ce qui peut √™tre relatif aux requ√™tes que nous pouvons faire.

const url = "http://localhost:8888/players/";
const fetchData = {
  // cette fonction permet de r√©cup√®rer tout les joueurs.
  // comme on fait un appel vers notre back, on utilisera une fonction asynchrone.
  getAllPlayers: async () => {
    const allPlayersData = await fetch(url, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
      mode: "cors",
      cache: "no-cache",
      credentials: "same-origin",
      redirect: "follow",
      referrerPolicy: "no-referrer",
    })
      .then((res) => res.json())
      .catch((err) => {
        console.log(err);
      });

    return allPlayersData;
  },
  // cette fonction permet de cr√©er le score d'un joueur.
  // elle prendra en param√®tre: le userName, le temps et les coups.
  // comme on fait un appel vers notre back, on utilisera une fonction asynchrone.
  post: async (data) => {
    const postScore = await fetch(url, {
      method: "POST",
      body: JSON.stringify(data),
      headers: {
        "Content-Type": "application/json",
      },
      mode: "cors",
      cache: "no-cache",
      credentials: "same-origin",
      redirect: "follow",
      referrerPolicy: "no-referrer",
    })
      .then((res) => console.log(res.json()))
      .catch((err) => {
        console.log(err);
      });
    return postScore;
  },
};

export { fetchData };
