import { card } from "./card/index";
import { gameBoard } from "./gameBoard";
import { leadershipBoard } from "./leadershipBoard";
import { userName } from "./userName";
import { get } from "./utils/get";

// lors du lancement de l'application, on ex√©cutera que app.init()

const app = {
  init: () => {
    // create.cards() est une fonction qui permet de cr√©er chaque carte que l'on a dans nos data.
    // Comme on a besoin d'une paires de chaque √©l√©ment, on ex√©cute deux fois la fonction.
    for (let i = 1; i <= 2; i++) card.createCards();
    // On change le titre de la page (onglet)
    document.title = "Memoxy";
    // On r√©cup√©re chaque √©l√©ments poss√©dant la class "card" (document.querySelectorAll)
    // J'ai choisi de cr√©er une fonction custom pour le cas ou j'avais besoin de cr√©er des execption.
    const cardsList = get.allByClass(".card");
    // On r√©cup√©re l'√©l√©ment parent de la premi√®re carte.
    const parentEl = cardsList[0].parentElement;

    // D√®s le lancement de la page, on :
    // - m√©lange le jeu fra√Æchement cr√©√©,
    // - on ajoute des events listener sur les boutons
    // - on r√©cup√©re la liste des 10 meilleurs joueurs
    gameBoard.shuffle(parentEl);
    gameBoard.resetBtn(cardsList);
    gameBoard.startBtn(cardsList);
    userName.inputForm();
    leadershipBoard.getListPlayers();
  },
};
// L‚Äô√©v√®nement load est √©mis lorsqu‚Äôune ressource et ses ressources d√©pendantes sont completement charg√©es.
document.addEventListener("load", app.init());
