import { leadershipBoard } from "./leadershipBoard";
import { timer } from "./timer";
import { move } from "./move";
import { get } from "./utils/get";
import { userName } from "./userName";

// Ici, on retrouvera tout ce qui peut √™tre relatif au bon fonctionnement du jeu.

// on instancie nos variables que l'on va r√©utiliser dans nos fonctions.
// isFlip pour savoir si la carte est retourn√©e ou non
// lockGame permet de locker le jeu, c'est √† dire que le joueur ne pourra pas retourner les cartes.
// firstCardSelected, secondCardSelected servent √† savoir quelle est la premi√®re  et la seconde carte s√©lectionn√©e par l'utilisateur
// start/resetButtonDOM permet de nous renvoyer l'√©l√©ment voulu.

let isFlip = false;
let lockGame = false;
let firstCardSelected, secondCardSelected;
const startButtonDOM = get.byId("start");
const resetButtonDOM = get.byId("reset");

// Si l'utilisateur n'a pas rentr√© de pseudo cet Array permettra d'en cr√©er.
const randomName = [
  "Yes",
  "No",
  "MinceAlors",
  "Rizon",
  "Top",
  "Lala",
  "Zut",
  "OhOh!",
  "Tacos",
  "Zone",
  "Bewan",
];

const gameBoard = {
  // cette fonction prends comme argument la liste de toutes les cartes qui ont √©t√© cr√©√©es
  startBtn: (cardsList) => {
    // on ajoute un event sur le bouton start
    startButtonDOM.addEventListener("click", () => {
      // on reset le timer
      timer.resetTimer();
      // on reset le board
      gameBoard.resetBoard();
      // on ajoute un event listener sur chaque carte avec la fonction flip
      cardsList.forEach((card) =>
        card.addEventListener("click", gameBoard.flip)
      );
      // on d√©marre le timer.
      timer.startTimer(startButtonDOM);
    });
  },

  // cette fonction prends comme argument la liste de toutes les cartes qui ont √©t√© cr√©√©es
  resetBtn: (cardsList) => {
    // on ajoute un event sur le bouton reset
    resetButtonDOM.addEventListener("click", () => {
      // on enl√®ve l'event listener sur chaque carte et on retire la class flip.
      cardsList.forEach((card) => {
        card.classList.remove("flip");
        card.removeEventListener("click", gameBoard.flip);
      });
      // on reset le timer
      timer.resetTimer();
      // on rm√©lange le jeu
      gameBoard.shuffle(cardsList[0].parentElement);
      // on reset le compteur de mouvement
      move.resetCount();
      // on lock le game
      lockGame = true;
    });
  },

  // cette fonction permet de savoir
  // - si on a selectionner la premi√®re carte ou la seconde carte
  // - de retourner la carte si le jeu n'est pas lock
  // - d'ingr√©menter le nombre de mouvement
  flip: (event) => {
    // ajoute +1 au nombre de mouvement
    move.addCount();
    const { parentElement } = event.target;
    // si le jeu est bloqu√© alors on n'ex√©cute pas le reste.
    if (lockGame) return;
    // si l'√©l√©ment parent s√©lectionn√© est √©gal √† la premi√®re carte s√©lectionn√©, alors on n'√©x√©cute pas le reste de la fonction.
    if (parentElement === firstCardSelected) return;
    // on ajoute la class flip au parent (√† notre div (div > img face | img retourn√©))
    parentElement.classList.add("flip");
    // si isFlip est true alors on √©x√©cute la condition.
    if (!isFlip) {
      isFlip = true;
      firstCardSelected = parentElement;
      return;
    }
    // si isFlip === true, que le jeu n'est pas bloqu√© et que l'√©l√©ment parent n'est pas √©gale √† la premi√®re carte s√©lectionn√© alors √ßa veut dire que l'on a s√©lectionn√© la seconde carte
    secondCardSelected = parentElement;
    // on va v√©rifier si les deux cartes sont les m√™mes.
    gameBoard.isMatch();
  },

  isMatch: () => {
    // si les data de chaque carte s√©lectionn√©es correspondent alors on d√©sactive ces cartes ou si elles ne correspondent pas, on les retourne.
    let isMatch =
      firstCardSelected.dataset.card === secondCardSelected.dataset.card;
    isMatch ? gameBoard.disable() : gameBoard.unFlip();
  },

  unFlip: () => {
    // on lock le jeu, pour √©viter que l'utilisateur retourne d'autres cartes.
    lockGame = true;
    setTimeout(() => {
      // on retire la class flip des cartes que l'on vient de s√©lectionner.
      firstCardSelected.classList.remove("flip");
      secondCardSelected.classList.remove("flip");
      // on reset le board cad (isFlip et lockgame = false)
      gameBoard.resetBoard();
    }, 500);
  },

  // Comme c'est un match, on retire les fonctions sur les cartes s√©lectionn√©es.
  disable: () => {
    firstCardSelected.removeEventListener("click", gameBoard.flip);
    secondCardSelected.removeEventListener("click", gameBoard.flip);
    // on v√©rifie ensuite si le jeu est termin√© ou non
    gameBoard.isWinOrLost();
    // on reset le board cad (isFlip et lockgame = false)
    gameBoard.resetBoard();
  },

  resetBoard: () => {
    // red√©finie les √©tats des variables comme elles √©taient au d√©but du jeu.
    [isFlip, lockGame] = [false, false];
    [firstCardSelected, secondCardSelected] = [null, null];
  },

  // Fonction qui permet de m√©langer le jeu suivant la m√©thode de : Fisher‚ÄìYates
  // Comme je n'√©tais pas satisfait de ma propre fonction j'ai pr√©f√©r√© en chercher une plus performante.
  // https://stackoverflow.com/questions/7070054/javascript-shuffle-html-list-element-order
  shuffle: (parentEl) => {
    for (let i = parentEl.children.length; i >= 0; i--) {
      parentEl.appendChild(parentEl.children[(Math.random() * i) | 0]);
    }
  },

  // Fonction permettant de savoir si le jeu :
  // - continue
  // - s'arr√™te √† cause du temps
  // ou si le joueur √† gagner.
  isWinOrLost: () => {
    // on r√©cup√®re le temps
    const time = timer.getSeconde();

    // on regarde le nombre de cartes retourn√©es
    const numberOfFlippedCard = get.allByClass(".flip").length;
    // on r√©cup√®re la liste de toutes les cartes.
    const cardsList = get.allByClass(".card");
    // on pr√©pare nous data dans un √©ventuel envoie vers notre serveur.
    const data = {
      userName:
        // s'il n'y a pas de pseudo rentr√© alors on en cr√©e un au hasard avec notre tableau ligne 22
        userName.get() ??
        `O'${randomName[Math.floor(Math.random() * randomName.length - 1)]}`,
      time,
      // on r√©cup√®re le nombre de coups jou√©s
      move: move.getCount(),
    };

    // si le nombre de cartes retourn√©es est √©gal au nombre de cartes dans tout le jeu et que le temps est inf√©rieur √† 60 alors :
    // - on v√©rouille le jeu
    // - on arr√™te le timer
    // - on envoie notre score
    // - on refresh le score
    // - on r√©cup√©re la liste du top 10.
    // - on met le nombre de coups √† 0
    // - on envoie une alerte annon√ßant la bonne nouvelle.
    if (numberOfFlippedCard == cardsList.length && time < 60) {
      lockGame = true;
      timer.timerCount(false);
      leadershipBoard.postScore(data);
      leadershipBoard.refresh();
      leadershipBoard.getListPlayers();
      move.resetCount();
      return alert(
        `C'est gagn√© ${data.userName} !!! Vous avez fait un score de ${
          data.time * data.move
        } (temps: ${data.time}s, move: ${data.move} )`
      );
    }

    // si le timer est sup√©rieur ou √©gal √† 60 alors c'est que le joueur √† perdu.
    // - on v√©rouille le jeu
    // - on arr√™te le timer
    // - on met le nombre de coups √† 0
    // - on envoie une alerte annon√ßant la mauvaise nouvelle.
    if (time >= 60) {
      lockGame = true;
      timer.timerCount(false);
      move.resetCount();
      alert(
        `C'est perdu ${
          data.userName
        } ... ${time}s se sont √©coul√©es, il manque ${(
          (cardsList.length - numberOfFlippedCard) /
          2
        ).toFixed(0)} ${
          (cardsList.length - numberOfFlippedCard) / 2 > 1 ? "paires" : "paire"
        }...`
      );
    }
    return;
  },
};

export { gameBoard };
